@import url(https://fonts.googleapis.com/css?family=Amatic+SC|Trade+Winds|Yeon+Sung&display=swap);
@import url(https://fonts.googleapis.com/css?family=Amatic+SC|Trade+Winds|Yeon+Sung&display=swap);
@import url(https://fonts.googleapis.com/css?family=Amatic+SC|Trade+Winds|Yeon+Sung&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#d-button-holder {
 width: 10rem;
 margin: auto;

}
/*DEVIL'S SNARE COMP*/
#d-options{
    display: inline-block;
}

#rain, #explosion, #sunlight, #scream, #torture, #dancing, #funny, #tickle{
    background: none;
    font-family: 'Yeon Sung', Arial;
    font-size: 1.125rem;
    border: none;
    margin-right: 0.5rem;
}

#mummy, #spider, #skrewt, #dementor, #hermione, #pettigrew, #neville, #snape {
    background: none;
    font-family: 'Yeon Sung', Arial;
    font-size: 1.125rem;
    border: none;
    margin-right: 0.5rem;
}

/*CHAMBER COMP*/
#hat, #sword, #basilisk, #gryffindor {
    background: none;
    font-family: 'Yeon Sung', Arial;
    font-size: 1rem;
    border: none;
    margin-right: 0.5rem;
}

/*INSTRUCTIONS COMP*/
#p-instructions{
    font-size: 1rem;
    text-align: left;
    margin-top: 0;
}

#d-instructions {
    margin: 1vh 0 1vh 0;
    text-align: center;
    width: 50rem;
    margin-top: 5vh;
    background-color: white;
    border-radius: 0.5rem;
    background: rgba(255, 255, 255, 0.85);
    display: inline-block;
}

#d-instructions:hover {
    background: rgba(255, 255, 255, 1.0);
}
